* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

body {
  background-color: #f6f6f1;
}

.all {
  background-color: #f6f6f1;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 20px;
  min-height: 100vh;
  padding: 20px;
  display: flex;
}

.card {
  cursor: pointer;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border: 2px solid #cdc9c9;
  border-radius: 12px;
  justify-content: center;
  align-items: end;
  width: 220px;
  height: 300px;
  transition: box-shadow .3s, transform .3s;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 8px #0000001a;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px #35e4494d;
}

.content {
  color: #fff;
  text-align: center;
  background-color: #da5c37d9;
  width: 100%;
  padding: 10px;
  font-size: 14px;
  position: absolute;
  bottom: 0;
}

.input-container {
  text-align: center;
  width: 300px;
  margin-bottom: 30px;
}

.my-input {
  border: 2px solid #333;
  border-radius: 8px;
  outline: none;
  width: 100%;
  padding: 12px;
  font-size: 16px;
  transition: border-color .3s, box-shadow .3s;
}

.my-input:focus {
  border-color: #da5c37;
  box-shadow: 0 0 5px #da5c3780;
}

.my-input::placeholder {
  color: #888;
  font-style: italic;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #da5c37;
  border: none;
  border-radius: 8px;
  margin-top: 10px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

button:hover {
  background-color: #c04b2f;
}
